




























































































































$primary: #409eff;
.gt-dialog {
  &.gt-dialog--theme {
    /deep/ .el-dialog__header {
      padding: 0 20px;
    }
    .gt-dialog__title {
      line-height: 42px;
    }
  }
  &.gt-dialog--primary {
    /deep/ .el-dialog__header {
      background-color: $primary;
      color: #fff;
    }
    /deep/ .el-dialog__headerbtn {
      top: 11px;
      right: 11px;
      .el-dialog__close {
        color: #ddd;
        &:hover {
          color: #fff;
        }
      }
    }
    /deep/ .el-dialog__footer {
      padding: 15px 20px;
    }
  }
  /deep/ .el-dialog__body {
    padding: 0;
  }
  .gt-dialog__body {
    padding: 20px;
    min-height: 100px;
    overflow-y: auto;
  }
}
