/**
 *  FOR framework and biz
 *
**/

// base color
$blue:#324157;
$light-blue:#3A71A8;
$red:#C03639;
$pink: #E65D6E;
$green: #30B08F;
$tiffany: #4AB7BD;
$yellow:#FEC171;
$panGreen: #30B08F;

// sidebar
$menuText:#818181;
$menuActiveText:#000;
$subMenuActiveText:#7e7e7e; // https://github.com/ElemeFE/element/issues/12951

$menuBg:#f4f4f4;
$menuHover:#ddd;

$subMenuBg:#f4f4f4;
$subMenuHover:#dfdfdf;

$sideBarWidth: 180px;
$sideBarLogoHeight: 83px;
// the :export directive is the magic sauce for webpack
// https://www.bluematador.com/blog/how-to-share-variables-between-js-and-sass
:export {
  menuText: $menuText;
  menuActiveText: $menuActiveText;
  subMenuActiveText: $subMenuActiveText;
  menuBg: $menuBg;
  menuHover: $menuHover;
  subMenuBg: $subMenuBg;
  subMenuHover: $subMenuHover;
  sideBarWidth: $sideBarWidth;
  sideBarLogoHeight: $sideBarLogoHeight;
}
