





























































.gt-table-template{
  bottom: 0px;
  left: 0px;
  right: 0px;
  display: flex;
  flex-direction: column;
}

.gt-table-template__top-block{
  margin-bottom: 5px;
}

.gt-table-template--page{
  position: absolute;
  top: 84px;
  padding: 15px 15px 2px 15px;
}

.gt-table-template__search-form{
  width: 100%;
  .opt-btns-wrap{
    float: right;
    margin-right: 0;
  }
}

.gt-table-template__search-result{
    flex: 1;
    overflow: hidden;
    .el-button+.el-button{
      margin-left: unset;
    }
}


.gt-pagination{
  margin-top: 20px;
}

.dialog-footer{
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}
